@font-face {
  font-family: 'Poppins-Light';
  src: url("../../src/fonts/Poppins-Light.ttf") format("truetype"); }

@font-face {
  font-family: 'Poppins-Bold';
  src: url("../../src/fonts/Poppins-Bold.ttf") format("truetype"); }

@font-face {
  font-family: 'Poppins-Regular';
  src: url("../../src/fonts/Poppins-Regular.ttf") format("truetype"); }

* {
  box-sizing: border-box; }

.container {
  max-width: 1240px;
  margin: auto; }

.container-grey {
  background-color: #F9F9FF; }

header {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  header .main-nav {
    background-color: #545454; }
    header .main-nav__list {
      display: flex;
      justify-content: center; }
    header .main-nav__item {
      font-family: 'Roboto', sans-serif;
      padding: 10px 20px 10px 20px;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 19px;
      color: white;
      cursor: pointer;
      transition: 0.3s; }
      header .main-nav__item:hover {
        background-color: #F4902F; }
      header .main-nav__item a {
        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 19px;
        color: white;
        text-decoration: none; }
      header .main-nav__item:first-child {
        margin-left: -20px; }
  header .contact-section {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px 0; }
    header .contact-section__logo {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: flex-start; }
    header .contact-section__btn {
      background-color: #23C4D2;
      border-radius: 100px;
      padding: 12px 20px;
      margin-right: 20px;
      cursor: pointer;
      transition: 0.3s; }
      header .contact-section__btn:hover {
        background-color: #F4902F;
        filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.25)); }
      header .contact-section__btn span {
        color: white;
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 27px; }
    header .contact-section__contacts {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column; }
      header .contact-section__contacts a {
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 21px;
        text-decoration: none;
        color: black;
        transition: 0.3s; }
        header .contact-section__contacts a:hover {
          opacity: 0.7; }

.header-slider {
  width: 100%;
  height: auto; }
  .header-slider .slick-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #545454;
    padding: 10px 0; }
    .header-slider .slick-dots li {
      margin: 0 5px;
      cursor: pointer; }
      .header-slider .slick-dots li:before {
        content: "";
        display: block;
        border-radius: 50%;
        background-color: white;
        border: 1px solid black;
        width: 15px;
        height: 15px; }
      .header-slider .slick-dots li.slick-active:before {
        background-color: #F4902F; }
      .header-slider .slick-dots li button {
        display: none; }
  .header-slider__item {
    height: auto;
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: contain;
    padding: 50px 0 50px 0; }
  .header-slider__title {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 60px;
    margin-bottom: 20px; }
  .header-slider__desc {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 30px;
    padding-right: 34%;
    margin-bottom: 100px; }
  .header-slider__more {
    background-color: #F4902F;
    border-radius: 100px;
    padding: 12px 20px;
    cursor: pointer;
    color: #fff;
    font-family: Poppins,sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    margin-left: calc((100% - 1240px) / 2);
    transition: 0.3s; }
    .header-slider__more:hover {
      background-color: #23C4D2;
      filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.25)); }

.start-date {
  display: flex; }
  .start-date__digit {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 500px;
    color: #23C4D2;
    position: relative; }
    .start-date__digit:before {
      position: absolute;
      content: "";
      display: block;
      height: 50%;
      width: 100%;
      top: 0;
      left: 0;
      background-color: rgba(255, 255, 255, 0.5); }
  .start-date__description {
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    margin-left: auto; }
  .start-date__desc-title {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 60px;
    color: transparent;
    -webkit-text-stroke: 2px #F4902F; }
  .start-date__desc-subtitle {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 80px;
    color: transparent;
    -webkit-text-stroke: 2px #23C4D2; }
  .start-date__read-more {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 50px;
    padding-left: 50%;
    position: relative; }
    .start-date__read-more svg {
      position: absolute;
      left: calc(50% - 100px);
      bottom: 20px;
      z-index: -1; }
    .start-date__read-more a {
      font-family: Roboto, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 23px;
      color: #F4902F; }

.acardeon__list {
  display: flex; }

.acardeon__title-closed {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 42px;
  color: #000000;
  display: none; }

.acardeon__icon {
  position: absolute;
  bottom: 20px;
  left: 20px; }

.acardeon__item {
  -webkit-box-shadow: 10px 0px 9px -2px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 10px 0px 9px -2px rgba(0, 0, 0, 0.57);
  box-shadow: 10px 0px 9px -2px rgba(0, 0, 0, 0.57);
  position: relative;
  min-height: 300px;
  flex: 1;
  cursor: pointer;
  transition: 0.3s; }
  .acardeon__item:hover {
    opacity: 0.7; }
  .acardeon__item.active {
    cursor: auto;
    flex: 0 0 50%;
    padding: 30px 30px 30px calc((100vw - 1240px) / 2); }
    .acardeon__item.active:hover {
      opacity: 1; }
    .acardeon__item.active .acardeon__lnk {
      display: block; }
    .acardeon__item.active .acardeon__desc {
      display: block; }
    .acardeon__item.active .acardeon__title-closed {
      display: block; }
    .acardeon__item.active .acardeon__title {
      display: none; }
    .acardeon__item.active .acardeon__desc {
      display: block;
      font-family: Poppins, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 30px;
      color: #000000; }
  .acardeon__item:nth-child(1) {
    background-color: #F4902F;
    z-index: 5; }
  .acardeon__item:nth-child(2) {
    background-color: #7FC04A;
    z-index: 4; }
  .acardeon__item:nth-child(3) {
    background-color: #23C4D2;
    z-index: 3; }
  .acardeon__item:nth-child(4) {
    background-color: #C4C4C4;
    z-index: 2; }
  .acardeon__item:nth-child(5) {
    background-color: #F0D740;
    z-index: 1; }

.acardeon__desc {
  display: none; }

.acardeon__title {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 100%;
  text-align: right;
  color: #000000;
  position: absolute;
  transform: rotate(-90deg);
  transform-origin: right bottom 0;
  top: -20px;
  right: 20px;
  min-width: 230px; }

.acardeon__lnk {
  text-decoration: none;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  color: #000000;
  border-radius: 10px;
  background-color: #C4C4C4;
  padding: 10px 20px;
  transition: 0.3s;
  position: absolute;
  right: 30px;
  bottom: 20px;
  display: none; }
  .acardeon__lnk:hover {
    background-color: white; }

.fullcycle {
  padding-top: 50px; }
  .fullcycle__desc {
    font-family: Poppins, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 36px;
    text-align: justify;
    color: rgba(0, 0, 0, 0.5);
    width: 50%; }
  .fullcycle__title {
    font-family: Poppins,sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 110px;
    color: transparent;
    -webkit-text-stroke: 2px #23C4D2;
    text-align: right; }
  .fullcycle__content {
    display: flex;
    padding: 40px 0;
    justify-content: space-between; }
  .fullcycle__image {
    margin-top: 55px;
    width: 40%;
    height: auto;
    position: relative; }
    .fullcycle__image:before {
      position: absolute;
      content: "";
      display: block;
      background-color: transparent;
      border: 20px solid #F4902F;
      left: -55px;
      top: -55px;
      width: 100%;
      height: 90%;
      z-index: 1; }
    .fullcycle__image img {
      z-index: 2;
      position: relative; }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
  touch-action: pan-y; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0); }

.slick-track:before, .slick-track:after {
  content: "";
  display: table; }

.slick-track:after {
  clear: both; }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: block; }
